body, html {
  width: 100%;
  height: 100%;
  margin: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  height: 100%;
  width: 100%;
  margin: 0;
}

.quotes {
    display:grid;
    margin: 15px;
    height: 80%;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto;
    grid-template-areas:
    "quote quote"
    ". author";
    justify-content: center;
}

.quotes p {
    margin: 10px;
    grid-area: quote;
    justify-self: center;
    font-weight: bold;
}

.author {
    grid-area: author;
    display: flex;
    justify-self: flex-end;
    align-self: end;
    margin-right: 15px;
}
.manager {
    display: grid;
    width: 100%;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto;
    grid-template-areas:
    "social button"
}

.social {
    display: flex;
    grid-area: social;
    justify-content: center;
    align-items: center;
    padding: 5px;
}

.buttonBox {
    display: flex;
    grid-area: button;
    justify-content: center;
    align-items: center;
}

.button {
    display: flex;
    width: 40%;
    height: 40px;
    justify-content: center;
    align-items: center;
    border-radius: 4px;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.box {
    display: grid;
    align-self: center;
    width: 40%;
    height: 250px;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr;
    grid-template-areas:
    "allquote"
    "manage";
    border-radius: 5px;
    padding: 10px;
    background-color: white;
    color: black;
    justify-content: center;
}
.App {
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: center;
}

